
.endscreen__entering {
  position: absolute;
  top: 150%;
  transform: translate(0, -50%);
  padding: 0 10vw;
}


.endscreen {
  transition: 750ms;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  padding: 0 10vw;
}

.endscreen__exiting {
  transition: 750ms;
  position: absolute;
  top: -50%;
  transform: translate(0, -50%);
  padding: 0 10vw;
}


.endscreen__text {
/*  position: absolute;
